}

.form .btn {
  font-size: 0.8125rem;
  padding: 20px 97px;
  align-self: flex-end;
  margin-top: 20px;
}

.footer {
  box-shadow: 0 -5px 40px #00000029;
  padding: 20px 0;
}

.footer__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__info {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.footer__copy {
  font-size: 0.6875rem;
  line-height: 1.5rem;
  color: var(--color-grey);
  margin-top: 6px;
}

.footer__icons a:first-of-type {
  margin-right: 25px;
}

@media (max-width: 375px) {
  .contact {
    padding: 0 1rem;
    display: flex;
    flex-direction: column;
  }
  .contact__column {
    width: 100%;
  }
  .contact__details {
    display: flex;
    flex-direction: column;
    width: 375px;
  }
  .contact__details a {
    display: block;
  }
}
@media (max-width: 375px) {
  li.nav__element {
    padding: 10px;
  }

  .statistics__container {
    display: block;
    margin: 125px;
  }

  .btn--large {
    padding: 27px 93px;
  }

  .pricing__box {
    width: 300px;
    margin-bottom: 3rem;
  }

  .pricing__box .pricing__box--professional {
    width: 330px;
    margin-bottom: 3rem;
  }

  .pricing__boxes {
    flex-direction: column;
  }
  .pricing__boxes div.pricing__box.pricing__box--professional {
    width: 330px;
  }
  .pricing__boxes div {
    flex-direction: row;
    flex-wrap: wrap-reverse;
  }
  .pricing__boxes div .pricing__details {
    flex: 1 0 40%;
  }
  .pricing__boxes div .pricing__items {
    flex: 2 0 50%;
  }
  .pricing__boxes .btn {
    order: -1;
    margin: 0 70px;
  }
}
@media (max-width: 900px) {
  .navbar .nav-container {
    display: block;
    position: relative;
    height: 60px;
  }
  .navbar .menu-items {
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
    display: none;
    background-color: white;
  }

  .nav-container input[type=checkbox]:checked ~ {
    height: 26px;
    width: 32px;
    position: absolute;
    top: 17px;
    right: 20px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .nav-container input[type=checkbox]:checked ~ .menu-items {
    display: block;
    width: 100%;
    transform: translateY(22%);
    transition: transform 0.5s ease-in;
  }
  .nav-container input[type=checkbox]:checked ~ .menu-items li {
    display: block;
  }
  .nav-container input[type=checkbox]:checked ~ .navbar .menu-items {
    display: block;
    transition: transform 0.5s ease-in-out;
  }
}
@media only screen and (min-width: 900px) {
  .nav-container input[type=checkbox] ~ .navbar {
    height: 70px;
  }
  .nav-container input[type=checkbox] ~ .menu-items {
    display: flex;
    height: 30px;
    width: 100%;
    justify-content: flex-end;
  }
  .nav-container input[type=checkbox] ~ .hamburger-lines {
    display: none;
  }
}
